
h1 {
  text-align: center;
}

.container {
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
}

.keyboards {
  padding: 1rem;
}

.highOctaveKeyboard {
  margin-bottom: 1rem;
}

.footer {
  margin-top: 3rem;
}

.config {
  width: 100%;
  display: grid;
  grid-template-columns: 5fr 1fr 11fr;
  align-items: center;
  grid-gap: 1rem;
}

.config .config-display-input {
  width: 3rem;
  text-align: center;
}

.config input[type='range'] {
  width: 100%;
}
